import {
    ORDER_CREATE_FAIL, 
    ORDER_CREATE_REQUEST, 
    ORDER_CREATE_SUCCESS,
    ORDER_DETAILS_REQUEST, ORDER_DETAILS_SUCCESS, ORDER_DETAILS_FAIL,
    ORDER_PAY_FAIL, ORDER_PAY_REQUEST, ORDER_PAY_SUCCESS,
    ORDER_LIST_FAIL, ORDER_LIST_MY_FAIL, ORDER_LIST_MY_REQUEST,
    ORDER_LIST_MY_SUCCESS, ORDER_LIST_REQUEST, ORDER_LIST_SUCCESS,
    ORDER_DELIVER_FAIL, ORDER_DELIVER_REQUEST, ORDER_DELIVER_SUCCESS
} from '../constants/orderConstants';
import { CART_CLEAR_ITEMS } from '../constants/cartConstants'

import { logout } from './userActions'

import axios from 'axios';


    export const createOrder = (order) => async (dispatch, getState) => {
        dispatch({type: ORDER_CREATE_REQUEST});
        const { userInfo } =getState().userLogin;
        try{
            const config = {
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': `Bearer ${userInfo.token}`
                },
            }
            const res = await axios.post(`/api/orders`, order, config);
            dispatch({type: ORDER_CREATE_SUCCESS, payload: res.data});

            dispatch({
                type: CART_CLEAR_ITEMS,
                payload: res.data,
              })
              localStorage.removeItem('cartItems')
           
        }catch(error){
           
            console.log(error.response.data);
            const message =
            error.response && error.response.data.message
              ? error.response.data.message
              : error.message

            if(message === 'Not authorized, token failed'){
                dispatch(logout())
            }

            dispatch({type: ORDER_CREATE_FAIL, payload: message});
        }
    
    }

    export const getOrderDetails = (id) => async (dispatch, getState) => {
        dispatch({type: ORDER_DETAILS_REQUEST});
        const { userInfo } =getState().userLogin;
        try{
            const config = {
                headers: {
                    'Authorization': `Bearer ${userInfo.token}`
                },
            }
            const res = await axios.get(`/api/orders/${id}`, config);
            console.log(res.data)
            dispatch({type: ORDER_DETAILS_SUCCESS, payload: res.data});
           
        }catch(error){
            const message =
            error.response && error.response.data.message
                ? error.response.data.message
                : error.message
            if (message === 'Not authorized, token failed') {
            dispatch(logout())
            }
            dispatch({
            type: ORDER_DETAILS_FAIL,
            payload: message,
            })
        }
    
    }

    export const payOrder = (orderId, paymentResult) => async (
        dispatch,
        getState
      ) => {
        try {
          dispatch({
            type: ORDER_PAY_REQUEST,
          })
      
          const {
            userLogin: { userInfo },
          } = getState()
      
          const config = {
            headers: {
              'Content-Type': 'application/json',
              Authorization: `Bearer ${userInfo.token}`,
            },
          }
      
          const { data } = await axios.put(
            `/api/orders/${orderId}/pay`,
            paymentResult,
            config
          )
      
          dispatch({
            type: ORDER_PAY_SUCCESS,
            payload: data,
          })
        } catch (error) {
          const message =
            error.response && error.response.data.message
              ? error.response.data.message
              : error.message
          if (message === 'Not authorized, token failed') {
            dispatch(logout())
          }
          dispatch({
            type: ORDER_PAY_FAIL,
            payload: message,
          })
        }
      }

